import React from 'react'
import { connect } from 'react-redux'
import AccountTable from './AccountTable'
import AccountDetail from './AccountDetail'

class App extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      error: null,
      isLoaded: false,
      // 是否展示查看详情结构块
      isWarningBlock: true,
      // 账户权限搜索字段
      accountSearch: '',
    }
  }
  componentDidMount() {

    this.props.dispatch({
      type: 'account/fetchCurrentAccount',
    });
  }

  // 增加账户
  addAccount = () => {
    this.props.dispatch({
      type: 'account/change',
      payload: {
        isAddAccount: true
      }
    });
  }

  // 查看配置权限清单
  handleAccountDetail = () => {
    this.props.dispatch({
      type: 'account/change',
      payload: {
        isAuthorityList: true
      }
    });
  }

  // 关闭权限配置清单结构块
  handleWarningBlockHide = () => {
    this.setState({
      isWarningBlock: false
    });
  }

  // 监听字段搜索输入框的字段
  handleAccountSearchInfo = (event) => {
    this.setState({
      accountSearch: event.target.value
    });
    console.log("搜索信息" + event.target.value);
  }

  // 执行搜索操作
  handleChangeSearch = () => {
    console.log("执行搜索操作");
  }

  render() {
    if (this.props.currentAccountInfo.groupId == 12) {
      return <span>你没有权限</span>
    }
    // 判断展示权限配置清单还是展示权限列表
    if (this.props.isAuthorityList) {
      return (
        <AccountDetail />
      );
    } else {
      return (
        <div className="main">
          <div className="manage-area">
            <div className="manage-area-title">
              <h2>账号权限</h2>
            </div>
            <div className="manage-area-main">
              {
                this.state.isWarningBlock == true
                  ? <div className="tc-15-msg warning">
                    <div className="tip-info">账户类型和权限暂时不支持自定义，&nbsp;不同类型的账户及拥有的权限请&nbsp;<a href="javascript:;" onClick={this.handleAccountDetail}>点击查看</a></div>
                    <a href="javascript:;" onClick={this.handleWarningBlockHide} className="tc-icon-btn"><i className="close-icon"></i></a>
                  </div>
                  : null
              }
              <div className="tc-action-grid">
                <div className="justify-grid">
                  <div className="col">
                    <button className="tc-15-btn m btn-add" onClick={this.addAccount}>新建账户</button>
                  </div>
                  {/* <div className="col">
                    <div className="tc-15-search tc-15-multi-search">
                      <textarea placeholder="搜索账户信息" className="tc-15-search-words"  value={this.state.accountSearch} onChange={this.handleAccountSearchInfo} ></textarea> 
                      <button className="tc-15-btn weak m search" onClick={this.handleChangeSearch}></button>
                    </div>
                  </div> */}
                </div>
              </div>
              <AccountTable />
            </div>
          </div>
        </div>
      );
    }
  }
}


const mapStateToProps = ({ account }) => {
  return {
    isAuthorityList: account.isAuthorityList,
    currentAccountInfo: account.currentAccountInfo
  }
}
export default connect(mapStateToProps)(App)
