import React from 'react'
import { connect } from 'react-redux'
import { DataTable, Pager, Input, FlexibleList } from '@tencent/bere'
import { Popconfirm } from '@plugin_components'
import AccountAuthorityList from './AccountAuthorityList'
import AddAccount from './AddAccount'

// 设置表单头
const columns = [
    { key: "username", title: "账户", editKey: 'user_type' },
    { key: "pwd", title: "密码" },
    { key: "user_type", title: "账户类型" },
    { key: "rights", title: "账户权限" },
    { key: "_operation", title: "操作" },
]

const RenderConfirmDel = (tempData) => {
    const { data, onOK, onCancel } = tempData;
    return (
        <div className="tc-15-confirm-popout tc-15-confirm-popout-top align-end" style={{ position: 'absolute', top: '25px', right: '-15px', zIndex: 9999 }}>
            <div className="tc-15-confirm-popout-bd">
                <p className="tc-15-msg"><strong>确定删除当前账户？ </strong>
                    <br /> 删除后将无法恢复。
                </p>
            </div>
            <div className="tc-15-confirm-popout-ft">
                <button className="tc-15-btn m" onClick={() => onOK(data)}>删除</button>
                <button className="tc-15-btn m weak" onClick={() => onCancel()}>取消</button>
            </div>
        </div>
    )
}
// 权限管理权限表格
class AccountTable extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            user_name: '',
            pwd: '',
            groupId: '',
            testPwdLetter: true,
            testUsernameLength: true
        }
    }

    /**
     *首次dom加载完就执行
     */
    componentDidMount() {
        this.props.dispatch({
            type: 'account/change',
            payload: {
                limit: 10
            }
        });
        this.props.dispatch({
            type: 'account/fetchGroup',
        });
        this.props.dispatch({
            type: 'account/fetchAuthScopes',

        });
        this.fetchAccount(1)
    }
    fetchAccount(page) {
        let { limit } = this.props;
        this.props.dispatch({
            type: 'account/fetchAccount',
            payload: {
                page: page,
                results_per_page: limit
            }
        });
    }
    get columns() {
        let _ = this;
        return DataTable.injectColumns(columns, {
            username: {
                render(value, data, index) {
                    return (
                        // data.isEdit 
                        // ?<div className= { _.state.testUsernameLength == false
                        //     ?"is-error"
                        //     :"form-unit"}>
                        //     <Input className="tc-15-input-text m" onChange={ (e) => _.changeInputUsername(e) } placeholder = { data.user_name } value={_.state.user_name}></Input>
                        //     {
                        //         _.state.testUsernameLength==false?<p className="form-input-help">账户不能为空</p>:""
                        //     }
                        // </div>
                        // :<div>
                        //     <span className="text-overflow">{data.user_name}</span>
                        // </div>
                        <div>
                            <span className="text-overflow">{data.username}</span>
                        </div>
                    );
                }
            },
            pwd: {
                render(value, data, index) {
                    return (
                        data.isEdit
                            ? <div className={_.state.testPwdLetter == false
                                ? "is-error"
                                : "form-unit"}  >
                                <Input className="tc-15-input-text m" onChange={(e) => _.changeInputPwd(e)} placeholder={data.pwd} value={_.state.pwd} onBlur={(e) => _.handleTestPwd(e)} ></Input>
                                {
                                    _.state.testPwdLetter == false ? <p className="form-input-help">必须8个字符以上且同时包含数字和字母</p> : ""
                                }
                            </div>
                            : <div>
                                {/* <span className="text-overflow">{data.pwd}</span> */}
                                <span className="text-overflow">************</span>
                            </div>
                    );
                }
            },
            rights: {
                render(value, data, index) {
                    const { scopesList } = _.props
                    return (
                        <AccountAuthorityList scopesList={scopesList} scopes={data.scopes} />
                    );
                }
            },
            user_type: {
                render(value, data, index) {
                    const { groupList, currentAccountInfo } = _.props
                    let group = groupList.find(el => {
                        return el.id == data.groupId
                    })
                    return (
                        data.isEdit
                            ? <div>
                                <select defaultValue={data.groupId} className="tc-15-select m" value={_.groupId} onChange={(e) => _.changeSelectUsertype(e)}>
                                    {
                                        groupList.map(el => {
                                            if (currentAccountInfo.groupId == 10 && el.id != 10) {
                                                return <option value={el.id} key={el.id}>{el.name}</option>
                                            } else if (currentAccountInfo.groupId == 11 && el.id == 12) {
                                                return <option value={el.id} key={el.id}>{el.name}</option>
                                            } else {
                                                return null
                                            }

                                        })
                                    }


                                </select>
                            </div>
                            : <div>
                                <span className="text-overflow">{group.name}</span>
                            </div>
                    );
                }
            },
            _operation: {
                width: 150,
                render(value, data, index) {
                    if (_.props.isAddAccount == false) {
                        if (_.props.isEditAccount == false) {
                            return (
                                <FlexibleList>
                                    {
                                        data.isEdit
                                            ? <React.Fragment>
                                                <a href="javascript:;"> 完成 </a>
                                                <a href="javascript:;" onClick={() => _.handleExit(data, index)}> 取消 </a>
                                            </React.Fragment>
                                            : <React.Fragment>
                                                <a href="javascript:;" onClick={() => _.handleEdit(data, index)}>编辑</a>
                                                <div style={{ position: 'absolute', display: 'inline' }}>
                                                    <Popconfirm
                                                        content={<RenderConfirmDel data={data} onOK={_.handleDelOK} onCancel={_.handleDelCancel} />}
                                                    >
                                                        <a href="javascript:;"> 删除 </a>
                                                    </Popconfirm>
                                                </div>
                                            </React.Fragment>
                                    }
                                </FlexibleList>
                            );
                        } else {
                            return (
                                <FlexibleList >
                                    {
                                        data.isEdit
                                            ? <React.Fragment>
                                                <a href="javascript:;" onClick={() => _.handleSave(data, index)}> 完成 </a>
                                                <a href="javascript:;" onClick={() => _.handleExit(data, index)}> 取消 </a>
                                            </React.Fragment>
                                            : <React.Fragment>
                                                <a className="disabled">编辑</a>
                                                <a className="disabled">删除</a>
                                            </React.Fragment>
                                    }
                                </FlexibleList>
                            );
                        }
                    } else {
                        return (
                            <div>
                                <a className="disabled"> 编辑</a>
                                <a className="disabled"> 删除</a>
                            </div>
                        );
                    }
                }
            }
        });
    }

    /**
    * 分页请求
    * current 1 2 3
    */
    handlePageChange(current) {
        this.props.dispatch({
            type: 'account/change',
            payload: {
                page: current
            }
        });
        this.fetchAccount(current + 1);
    }
    /**
    * 修改每页的数据
    * perPageCount 10 20 50
    */
    handlePerPageChange(perPageCount) {
        this.props.dispatch({
            type: 'account/change',
            payload: {
                limit: perPageCount
            }
        });

        this.fetchAccount(1);
    }

    // 失去焦点对输入密码进行效验
    handleTestPwd = (event) => {
        let testInput = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,}$/;
        this.setState({
            testPwdLetter: testInput.test(event.target.value),
        })
    }

    // 失去焦点对输入账户进行效验
    handleTestUserName = (event) => {
        let testInput = /\S{1,}/;
        this.setState({
            testUsernameLength: testInput.test(event.target.value)
        });
    }

    // 监听账户名
    changeInputUsername(e) {
        this.setState({
            user_name: e.target.value
        });
    }

    // 监听密码
    changeInputPwd(e) {
        this.setState({
            pwd: e.target.value
        });
    }

    // 监听账户类型
    changeSelectUsertype(e) {
        this.setState({
            groupId: e.target.value
        });
    }

    // 保存 完成
    handleSave(item, index) {
        let _ = this;
        // 规范的数据
        if (this.state.testPwdLetter && this.state.testUsernameLength) {
            let payload = {
                id: item.id,
                groupId: Number(this.state.groupId)
            }
            if (this.state.pwd != "") {
                payload.password = this.state.pwd
            }
            this.props.dispatch({
                type: 'account/fetchEditAccount',
                payload: {
                    id: item.id,
                    groupId: Number(this.state.groupId)
                },
                callback() {
                    _.fetchAccount(_.props.page + 1)
                }
            });
        }


        // 变为原来的编辑框
        let data = this.props.accountList;
        data[index].isEdit = false;

        // 数据初始化
        this.setState({
            pwd: '',
            groupId: ''
        });

        // 恢复行可操作
        this.props.dispatch({
            type: 'account/change',
            payload: {
                isEditAccount: false
            }
        });
    }

    // 取消编辑
    handleExit(item, index) {
        // 变为原来的编辑框
        let data = this.props.accountList;
        data[index].isEdit = false;

        // 数据初始化
        this.setState({
            user_name: '',
            pwd: '',
            groupId: ''
        });

        // 恢复行可操作
        this.props.dispatch({
            type: 'account/change',
            payload: {
                isEditAccount: false
            }
        });
    }
    // 编辑
    handleEdit(itemData, index) {
        this.setState({
            user_name: itemData.name,
            pwd: '',
            groupId: itemData.groupId
        });
        // 这里的写法待确定，后台联调可能需要修改
        let data = this.props.accountList;
        data = data.map((item, index) => {
            if (item.isEdit) {
                // this.save(index)
                console.log("这条数据进行过操作了");
            }
            return item;
        })
        data[index].isEdit = true;
        // 其余行置灰不可操作
        this.props.dispatch({
            type: 'account/change',
            payload: {
                isEditAccount: true
            }
        });
    }

    // 确认删除
    handleDelOK = (data) => {
        let _ = this;
        const { page } = this;
        this.props.dispatch({
            type: 'account/fetchDelAccount',
            payload: {
                id: data.id
            },
            callback() {
                _.fetchAccount(page + 1);
            }
        });
    }

    // 取消删除
    handleDelCancel = () => {

    }

    render() {
        return (
            <div>
                <DataTable
                    style={{ minHeight: 500 }}
                    rowKey="accountId"
                    error={this.props.error}
                    loading={this.props.loading}
                    columns={this.columns}
                    data={this.props.accountList}
                />
                {
                    this.props.isAddAccount &&
                    <AddAccount />
                }

                {
                    this.props.accountList && this.props.accountList.length > 0
                    &&
                    <Pager
                        total={this.props.total}
                        current={this.props.page}
                        perPageCount={this.props.limit}
                        perPageCountList={[10, 20, 50]}
                        onPageChange={val => this.handlePageChange(val)}
                        onPerPageChange={val => this.handlePerPageChange(val)}
                    />
                }

            </div>
        )
    }
}

const mapStateToProps = ({ account }) => {
    return {
        accountList: account.accountList,
        page: account.page,
        limit: account.limit,
        total: parseInt(account.total),
        loading: account.isGroupLoading,
        isAddAccount: account.isAddAccount,
        isEditAccount: account.isEditAccount,
        completedEdit: account.completedEdit,
        groupList: account.groupList,
        scopesList: account.scopesList,
        currentAccountInfo: account.currentAccountInfo
    }
}

export default connect(mapStateToProps)(AccountTable)
