import React from 'react'
import { connect } from 'react-redux'

// 增加账户
class AddAccount extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            user_name: '',
            pwd: '',
            groupId: '',
            testPwdLetter: true,
            testUsernameLength: true
        };
    }


    componentDidMount() {
        if (sessionStorage.getItem('newUserName')) {
            this.setState({
                user_name: sessionStorage.getItem('newUserName')
            });
        }
        if (sessionStorage.getItem('newPwd')) {
            this.setState({
                pwd: sessionStorage.getItem('newPwd')
            });
        }
    }

    // 获取设置账户名的输入情况
    handleChangeUserName = (event) => {
        this.setState({
            user_name: event.target.value
        });
    }

    // 获取设置密码的输入情况
    handleChangePwd = (event) => {
        this.setState({
            pwd: event.target.value
        });
    }

    // 失去焦点对输入密码进行效验
    handleTestPwd = (event) => {
        let testInput = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,}$/;
        this.setState({
            testPwdLetter: testInput.test(event.target.value),
        })
    }

    // 失去焦点对输入账户进行效验
    handleTestUserName = (event) => {
        let testInput = /\S{1,}/;
        this.setState({
            testUsernameLength: testInput.test(event.target.value)
        });
    }

    // 获取选择账户类别的输入情况
    handleChangeusertype = (event) => {
        this.setState({
            groupId: event.target.value
        });
    }


    // 查看详情
    handleAccountDetail = () => {
        if (this.state.user_name) {
            sessionStorage.setItem('newUserName', this.state.user_name);
        }
        if (this.state.pwd) {
            sessionStorage.setItem('newPwd', this.state.pwd);
        }
        this.props.dispatch({
            type: 'account/change',
            payload: {
                isAuthorityList: true
            }
        });
    }

    // 执行添加操作
    addEnforceAccount = () => {
        let _ = this;
        if (this.state.testPwdLetter == true && this.state.testUsernameLength == true) {
            if (this.state.user_name != "" && this.state.pwd != null && this.state.groupId != undefined) {
                this.storageTemporaryClear();
                this.props.dispatch({
                    type: 'account/change',
                    payload: {
                        isAddAccount: false
                    }
                });
                this.props.dispatch({
                    type: 'account/fetchAddAccount',
                    payload: {
                        username: this.state.user_name,
                        password: this.state.pwd,
                        groupId: Number(this.state.groupId)
                    },
                    callback() {
                        _.props.dispatch({
                            type: 'account/fetchAccount',
                            payload: {
                                page: 1,
                                num: 10
                            }
                        });
                    }
                });
            }
        }
    }

    // 取消增加账户
    exitAddAccount = () => {
        this.storageTemporaryClear();
        this.props.dispatch({
            type: 'account/change',
            payload: {
                isAddAccount: false
            }
        });
    }

    // 清除临时存储 并将数值初始化
    storageTemporaryClear() {
        sessionStorage.clear();
        this.setState({
            user_name: '',
            pwd: '',
            groupId: 'general',
            testPwdLetter: true,
            testUsernameLength: true
        });
    }


    render() {
        const { groupList, currentAccountInfo } = this.props
        return (
            <div className="tc-15-table-panel">
                <div className="">
                    <div className="tc-15-table-fixed-body">
                        <table className="tc-15-table-box tc-15-table-rowhover" style={{ tableLayout: "fixed" }}>
                            <colgroup>
                                <col ></col>
                                <col ></col>
                                <col ></col>
                                <col ></col>
                                <col style={{ width: 150 + "px" }}></col>
                            </colgroup>
                            <tbody className="table-tbody">
                                <tr className="">
                                    <td role="presentation">
                                        <div className={this.state.testUsernameLength == false
                                            ? "is-error"
                                            : "form-unit"}>
                                            <input type="text" placeholder="请输入账户名" className="tc-15-input-text m" value={this.state.user_name} onChange={this.handleChangeUserName} onBlur={this.handleTestUserName} />
                                            {
                                                this.state.testUsernameLength == false ? <p className="form-input-help">账户不能为空</p> : ""
                                            }
                                        </div>
                                    </td>
                                    <td role="presentation">
                                        <div className={this.state.testPwdLetter == false
                                            ? "is-error"
                                            : "form-unit"} >
                                            <input type="text" minLength="8" placeholder="请输入账户密码" className="tc-15-input-text m" value={this.state.pwd} onChange={this.handleChangePwd} onBlur={this.handleTestPwd} />
                                            {
                                                this.state.testPwdLetter == false ? <p className="form-input-help">必须8个字符以上且同时包含数字和字母</p> : ""
                                            }
                                        </div>
                                    </td>
                                    <td role="presentation">
                                        <div className="form-unit">
                                            <select defaultValue={groupList[0].id} className="tc-15-select m" value={this.state.groupId} onChange={(e) => this.handleChangeusertype(e)}>
                                                {
                                                    groupList.map(el => {
                                                        if (currentAccountInfo.groupId == 10 && el.id != 10) {
                                                            return <option value={el.id} key={el.id}>{el.name}</option>
                                                        } else if (currentAccountInfo.groupId == 11 && el.id == 12) {
                                                            return <option value={el.id} key={el.id}>{el.name}</option>
                                                        } else {
                                                            return null
                                                        }
                                                    })
                                                }


                                            </select>
                                        </div>
                                    </td>
                                    <td role="presentation">
                                        <div><span className="text-overflow"><a href="javascript:;" onClick={this.handleAccountDetail}>查看详情</a></span></div>
                                    </td>
                                    <td role="presentation">
                                        <div>
                                            <a href="javascript:;" onClick={this.addEnforceAccount}> 确定 </a>
                                            <a href="javascript:;" onClick={this.exitAddAccount}> 取消 </a>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );

    }
}

const mapStateToProps = ({ account }) => {
    return {
        query: account.query,
        groupList: account.groupList,
        currentAccountInfo: account.currentAccountInfo
    };
}

export default connect(mapStateToProps)(AddAccount)
