import React from 'react'
import Checkbox from './CheckBox'
import { enhanceWithClickOutside } from '@tencent/bere-hoc'
/**
 * 下拉菜单
 * 多选
 * 单选
 */
class DropDownList extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      showMenu: false,
      checkedList: props.defaultCheckedList ? props.defaultCheckedList : [],
      lastTimeCheckedList: props.defaultCheckedList ? props.defaultCheckedList : [],
      selectedText: ''
    }

    this.OnDropList = this.OnDropList.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onCancel = this.onCancel.bind(this);

  }
  static defaultProps = {
    list: [],
    checkable: false,//是否多选
    label: "请选择",
    defaultCheckedList: [],//多选默认选择中项
    value: "",//单选默认
    onSubmit: null,
    onSubmitAll: null,//额 需要value和text,
    emptyText: '无对应结果',
    disabled: false
  }
  setDefault(props) {
    const { checkable, value, list } = props;
    if (!checkable) {
      let info = list.find(el => {
        return el.value == value;
      })
      if (info) {
        this.setState({ selectedText: info.text });
      }
    }
  }
  componentDidMount() {
    this.setDefault(this.props);
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.list && this.props.list) {
      if (nextProps.list.toString() != this.props.list.toString()) {
        this.setState({
          checkedList: nextProps.defaultCheckedList ? nextProps.defaultCheckedList : [],
          lastTimeCheckedList: [],
          selectedText: nextProps.label
        })
        this.setDefault(nextProps);
      }
    }
    if (nextProps.value != this.props.value) {
      this.setDefault(nextProps);
    }
  }

  handleClickOutside = () => {
    this.setState({
      showMenu: false
    })
  }

  OnDropList() {
    const { lastTimeCheckedList } = this.state;
    let list = [];
    lastTimeCheckedList.forEach(item => {
      list.push(item);
    })

    this.setState({
      showMenu: true,
      checkedList: list
    })
  }
  onSubmit() {
    if (this.props.onSubmit) {
      this.props.onSubmit(this.state.checkedList);
    }
    const { checkedList } = this.state;
    let list = [];
    checkedList.forEach(item => {
      list.push(item);
    })
    this.setState({
      showMenu: false,
      lastTimeCheckedList: list
    })
  }
  onCancel() {
    this.setState({
      showMenu: false
    })
  }
  onCheck(value) {
    let { checkedList } = this.state;
    if (checkedList.length > 0) {
      let index = checkedList.indexOf(value);
      if (index != -1) {
        checkedList.splice(index, 1);
      } else {
        checkedList.push(value);
      }
    } else {
      checkedList.push(value);
    }
    this.setState({ checkedList: checkedList });
  }
  onSelect(item) {
    if (this.props.onSubmit) {
      this.props.onSubmit(item.value);
    }
    if (this.props.onSubmitAll) {
      this.props.onSubmitAll(item);
    }
    this.setState({
      showMenu: false,
      selectedText: item.text
    })
  }
  renderItem(item, index) {
    const { checkable } = this.props;
    const { checkedList } = this.state;
    let isChecked = false;
    if (checkedList && checkedList.length > 0)
      isChecked = checkedList.indexOf(item.value) != -1;
    return (
      <li role="presentation" className="tc-15-optgroup" key={index}>
        {
          checkable &&
          <label className="tc-15-checkbox-wrap" style={{ fontSize: "12px" }}>
            <input type="checkbox" className="tc-15-checkbox" value={item.value} checked={isChecked} onChange={(e) => { this.onCheck(item.value) }} />
            {item.text}
          </label>
        }
        {
          !checkable &&
          <a role="menuitem" onClick={e => { this.onSelect(item) }} >{item.text}</a>
        }

      </li>
    )
  }
  render() {
    const { list, checkable, label, emptyText, disabled } = this.props;
    const { showMenu, checkedList, selectedText } = this.state;
    let title = label;
    if (!checkable && selectedText != "") {
      title = selectedText;
    }
    let ListClass = "tc-15-dropdown tc-15-dropdown-btn-style";
    if (disabled) 
    {
      ListClass += " disabled"
    }
    return (
      <div className={ListClass} ref="droplist" >
        <a onClick={this.OnDropList} className="tc-15-dropdown-link">
          <em>{title}</em><i className="caret"></i>
        </a>
        {
          showMenu &&
          < div className="tc-15-filtrateu" style={{ width: "180px" }}>
            <ul className="tc-15-filtrate-menu" role="menu">
              {
                list.map((item, index) => {
                  return this.renderItem(item, index)
                }
                )
              }
            </ul>
            {
              checkable && list.length > 0 &&
              <div className="tc-15-filtrate-ft">
                <button className="tc-15-btn m" onClick={this.onSubmit}>确定</button>
                <button className="tc-15-btn m weak" onClick={this.onCancel}>取消</button>
              </div>
            }
            {
              list.length == 0 &&
              <div style={{ padding: "10px" }} className="text-weak">
                {emptyText}
              </div>
            }
          </div>
        }
      </div >
    )
  }
}
export default enhanceWithClickOutside(DropDownList)
