import typeVerify from './type'
import { maskMap, hairMap, libsMap, maleMap } from '../constant/user'
export function combinationUserInfo(data) {
    console.log("userinfo", data);
    let tablelist = [];
    if (data) {
        data.forEach((ele) => {
            tablelist.push({
                img: ele.capturePath,
                person_id: ele.targetid,
                type: ele.libTypeName,
                userinfo: ele.targetName,
                face_total: ele.appearCount + "次",
                cam_count: ele.cameraCount + "台",
                cost_time: Math.round(ele.period / 60) + "分钟",
                start_time: ele.firstTime,
                end_time: ele.lastTime,
                _operation: 1,
            })
        });
    }
    return tablelist;
}
export function filterCameraTreeInfo(groupTreeInfo) {
    let treeList = [], level = 0;
    function cycle(groupTreeInfo, treeList) {
        level++;
        groupTreeInfo.forEach((item) => {
            if (typeVerify.isArray(item.childs) && item.childs) {
                let clone = {
                    text: item.name,
                    value: item.id,
                    type: item.type,
                    parentgroup: item.parentgroup,
                    childrenlist: [],
                    level,
                }
                cycle(item.childs, clone.childrenlist);
                treeList.push(clone);
            } else {
                treeList.push({
                    text: item.name,
                    value: item.id,
                    type: item.type,
                    childrenlist: _deepCopy(item.childs) || [],
                    parentgroup: item.parentgroup,
                    level
                })
            }
        })
    }
    cycle(groupTreeInfo, treeList);
    return treeList;
}


export function getParentNodeByTag(elem, tag) {
    let result = null;

    function findParentNode(elem) {
        if (elem.parentNode && elem.parentNode != document.documentElement) {
            let pNode = elem.parentNode
            if (pNode.tagName.toLowerCase() == tag) {
                result = pNode;
            } else {
                findParentNode(pNode)
            }
        }
    }
    findParentNode(elem);
    return result;
}

export function _deepCopy(source) {
    let copy, i, len, prop;
    let _hasOwn = window["Object"]["prototype"]["hasOwnProperty"];
    if (typeof source !== "object" || source == null || typeof source.nodeType === "number") {
        copy = source;
    } else if (typeof source.length === "number") {
        copy = [];
        for (i = 0, len = source.length; i < len; i++) {
            if (_hasOwn.call(source, i)) {
                copy[i] = _deepCopy(source[i]);
            }
        }
    } else {
        copy = {};
        for (prop in source) {
            if (_hasOwn.call(source, prop)) {
                copy[prop] = _deepCopy(source[prop]);
            }
        }
    }
    return copy;
};
export function filterUserDetail(data, tablelist, person_id) {
    let userDetailInfo = {}
    let personInfo = data;
    let hair_length = personInfo ? personInfo.hairLength : "-1";
    let lib_name = personInfo ? (personInfo.faceLibid ? personInfo.faceLibid : "--") : "--";
    let mask = personInfo ? personInfo.mask : "-1";
    let age = personInfo ? personInfo.age : "--";
    let gender = personInfo ? personInfo.gender : "-1";
    if (gender === 50) {
        gender = 2;
    } else if (gender >= 50) {
        gender = 0;
    } else if (gender <= 50) {
        gender = 1;
    }
    let userInfo = getUserInfoByPersonID(tablelist, person_id);
    userDetailInfo.baseInfo = {
        type: lib_name,
        imgUrl: userInfo.img,
        age: age,
        male: maleMap[gender],
        hair_length: hairMap[hair_length],
        mask: maskMap[mask],
        face_total: userInfo.face_total,
        cam_count: userInfo.cam_count,
        cost_time: userInfo.cost_time,

    }
    userDetailInfo.time_info = data.time_info;
    return userDetailInfo;

}
export function getUserInfoByPersonID(tablelist, person_id) {
    for (let i = 0; i < tablelist.length; i++) {
        if (tablelist[i].person_id == person_id) {
            return tablelist[i];
        }
    }
}
export function filterTrackDayData(trackDaydata) {
    let trailinfoList = [];
    Object.keys(trackDaydata).forEach((key) => {
        trailinfoList.push(trackDaydata[key]);
    })
    return trailinfoList;
}
export function filterGuestData(guestInfo) {
    let { age, gender } = guestInfo;
    let ages = [], genders = [], ageKeys = [];
    age.forEach((ele) => {
        for (let key in ele) {
            ageKeys.push(key);
        }
    })
    ageKeys.forEach((key, index) => {
        ages.push({
            age: key,
            total: age[index][key].total,
            ratio: age[index][key].ratio,
        })
    });

    Object.keys(gender).forEach((key) => {
        genders.push({
            male: key,
            total: gender[key].total,
            ratio: gender[key].ratio,
        })
    });
    guestInfo.age = ages;
    guestInfo.gender = genders;
    return guestInfo;

}
export function prepend(source) {
    var copy = _deepCopy(source);
    return copy;
}

// 展开treelist
export function flattenToNode(list) {
    let childList = [];
    list.forEach(item => {
        if (item.childrenlist && item.childrenlist.length > 0) {
            childList = childList.concat(flattenToNode(item.childrenlist))
        } else {
            childList.push(item)
        }
    })

    return childList
}

// 展开grouplist
export function flattenToGroup(list) {
    let childList = [];
    list.forEach(item => {
        if (item.type == 'group') {
            childList.push(item)
            if (item.childrenlist) {
                childList = childList.concat(flattenToGroup(item.childrenlist))
            }
        }
    })

    return childList
}

export function pushParams(target, name, op, val) {
    target.push({ name, op, val });
}

