import React from 'react'
import { connect } from 'react-redux'
import { DataTable } from '@tencent/bere'

// 设置表单头
const columns = [
    { key: "one_level_primary", title: "一级功能项" },
    { key: "two_level_primary", title: "二级功能项" },
    { key: "three_level_primary", title: "三级功能项" },
    { key: "general", title: "普通账户权限" },
    { key: "admin", title: "管理员账户权限" },
    { key: "develop", title: "开发者账户权限" }
]

// 权限配置清单
class AccountDetail extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            funList: [{
                one_level_primary: "实时监控",
                two_level_primary: "无",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "布控策略",
                two_level_primary: "无",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "检索追溯",
                two_level_primary: "无",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "客群分析",
                two_level_primary: "无",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "用户分析",
                two_level_primary: "无",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "资源",
                two_level_primary: "设备管理",
                three_level_primary: "查看设备和分组信息",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "资源",
                two_level_primary: "设备管理",
                three_level_primary: "编辑和管理设备",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "资源",
                two_level_primary: "人脸库管理",
                three_level_primary: "查看人脸信息",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "资源",
                two_level_primary: "人脸库管理",
                three_level_primary: "编辑和管理人脸库",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "系统",
                two_level_primary: "账户权限管理",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "系统",
                two_level_primary: "历史记录",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "系统",
                two_level_primary: "操作日志",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "运维管理",
                two_level_primary: "设备服务器管理",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }, {
                one_level_primary: "开发者管理平台",
                two_level_primary: "无",
                three_level_primary: "无",
                general: "0",
                admin: "0",
                develop: "0"
            }]
        }
    }


    componentDidMount() {
        this.props.dispatch({
            type: 'account/fetchAuthorityList'
        });
    }

    get columns() {
        return DataTable.injectColumns(columns, {
            one_level_primary: {
                render(value, data) {

                    // return(
                    //     <div >
                    //         <span>{data.one_level_primary}</span>
                    //     </div>
                    // );

                    if (data.one_level_primary == "运维管理") {
                        return (
                            <div >
                                <span>{data.one_level_primary}</span>
                                {
                                    data.one_level_primary == "运维管理"
                                        ?
                                        <div className="tc-15-bubble-icon tc-15-triangle-align-start ">
                                            <i className="tc-icon icon-what"></i>
                                            <div className="tc-15-bubble tc-15-bubble-bottom">
                                                <div className="tc-15-bubble-inner">
                                                    <p>相关解释说明内容</p>
                                                </div>
                                            </div>
                                        </div>
                                        : null
                                }
                            </div>
                        );
                    } else {
                        return (
                            <div >
                                <span>{data.one_level_primary}</span>
                                {
                                    data.one_level_primary == "开发者管理平台"
                                        ?
                                        <div className="tc-15-bubble-icon tc-15-triangle-align-start ">
                                            <i className="tc-icon icon-what"></i>
                                            <div className="tc-15-bubble tc-15-bubble-bottom">
                                                <div className="tc-15-bubble-inner">
                                                    <p>相关解释说明内容</p>
                                                </div>
                                            </div>
                                        </div>
                                        : null
                                }
                            </div>
                        );
                    }
                }
            },
            two_level_primary: {
                render(value, data) {
                    return (
                        <div>
                            {
                                data.two_level_primary == "无"
                                    ? <span className="text-weak">{data.two_level_primary}</span>
                                    : <span>{data.two_level_primary}</span>
                            }
                        </div>
                    );
                }
            },
            three_level_primary: {
                render(value, data) {
                    return (
                        <div>
                            {
                                data.three_level_primary == "无"
                                    ? <span className="text-weak" >{data.three_level_primary}</span>
                                    : <span>{data.three_level_primary}</span>
                            }
                        </div>
                    );
                }
            },
            general: {
                render(value, data) {
                    return (
                        <div>
                            {
                                data.general == "0"
                                    ? <span className="text-danger" >不可操作</span>
                                    : <span>有</span>
                            }

                        </div>
                    );
                }
            },
            admin: {
                render(value, data) {
                    return (
                        <div>
                            {
                                data.admin == "0"
                                    ? <span className="text-overflow text-danger" >不可操作</span>
                                    : <span>有</span>
                            }
                        </div>
                    );
                }
            },
            develop: {
                render(value, data) {
                    return (
                        <div>
                            {
                                data.develop == "0"
                                    ? <span className="text-danger" >不可操作</span>
                                    : <span>有</span>
                            }
                        </div>
                    );
                }
            }
        });
    }

    // 返回权限列表
    handleBack = () => {
        this.props.dispatch({
            type: 'account/change',
            payload: {
                isAuthorityList: false
            }
        });
    }

    render() {
        if (this.props.funList.length > 0) {
            this.state.funList.map((item, index) => {
                // 需要修改的key
                let _temp = {
                    general: '0',
                    admin: '0',
                    develop: '0'
                }
                // 对应的三种type
                let _type = ['admin', 'develop', 'general'];

                _type.map(ty => {
                    let _obj = this.props.funList.find(d => ty == d.user_type);
                    _temp[ty] = _obj[`fun${index + 1}`]
                })
                item.admin = _temp.admin;
                item.general = _temp.general;
                item.develop = _temp.develop;
                return { ...item, ..._temp }
            })
        }

        let isAuthorityList = this.props.isAuthorityList;
        if (isAuthorityList) {
            return (
                <div className="main">
                    <div className="manage-area">
                        <div>
                            <div className="manage-area-title secondary-title">
                                <a className="back-link" href="javascript:;" title="返回" onClick={this.handleBack}><i className="btn-back-icon"></i></a>
                                <span className="line-icon">|</span>
                                <h2>功能权限配置清单</h2>
                            </div>
                            <div className="manage-area-main">
                                <DataTable rowKey="accountId"
                                    style={{ minHeight: 600 }}
                                    error={this.props.error}
                                    loading={this.props.isGroupLoading}
                                    columns={this.columns}
                                    data={this.state.funList} />
                            </div>
                        </div>
                    </div>
                </div>
            );
        } else {
            return null;
        }
    };
}

const mapStateToProps = ({ account }) => {
    return {
        funList: account.authorityList,
        isAuthorityList: account.isAuthorityList,
        isGroupLoading: account.isGroupLoading
    }
}
export default connect(mapStateToProps)(AccountDetail)

