import React from 'react'

/**
 * 复选框
 */
export default function Checkbox(props) {
  const { label, value, onChange, groupType, ...rest } = props

  function handleChange(e) {
    onChange(e, e.target.checked, e.target.value)
  }

  return (
    <label className="form-ctrl-label" style={{ fontSize: "12px" }}>
      <input
        type="checkbox"
        className="tc-15-checkbox"
        onChange={handleChange}
        value={value}
        grouptype={groupType}
        {...rest} />
      {label}
    </label>
  )
}
