import React, { PureComponent, Component } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

export default class Popconfirm extends Component {

  constructor(props){
    super(props)
    this.state = {
      isShow: false
    }
    this.show = this.show.bind(this)
  }

  show(){
    this.setState({
      isShow: !this.state.isShow
    })
    
  }

  render(){
    let {
      position = 'top',
      offset,
      children,
      className,
      style,
      content,
      trigger,
    } = this.props
    const { isShow } = this.state
    const typeCls = `tc-15-bubble-top`
    if( !isShow ){
      style = Object.assign({}, { fontSize: 'inherit'}, style)
    }else{
      style = Object.assign({}, { fontSize: 'inherit'}, style)
    }
    return (
      <div
        className='tc-15-bubble-icon'
        style={style}
        onClick={this.show}
      >
        { children }
        { 
          isShow && content
        }
      </div>
    )
  }
}

 