import React from 'react'
import Checkbox from './CheckBox'
import { getParentNodeByTag } from '../../utils/user'
import { enhanceWithClickOutside } from '@tencent/bere-hoc'
class DropDownTree extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showMenu: false,//默认是否显示菜单栏
            label: "选择设备",
            searchTreeList: '',//搜索结果
            keyword: '',
            isSearch: false
        }
        this.renderChildrenList = this.renderChildrenList.bind(this);
        this.onArrowChange = this.onArrowChange.bind(this);
        this.OnDropList = this.OnDropList.bind(this);
        this.onCheckBoxChange = this.onCheckBoxChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.HandleSearch = this.HandleSearch.bind(this);
        this.onSearchChange = this.onSearchChange.bind(this);
        this.onSearchKeyUp = this.onSearchKeyUp.bind(this);
    }
    static defaultProps = {
        treeList: [],
        arrow: "right",//默认指针是收起的
        hasfooter: true,
        onSubmit: null,
        canSearch: true
    }

    onSubmit() {
        let valuetrees = { group: [], cameras: [] };
        let inputElems = this.refs.droptree.querySelectorAll('input[type=checkbox]');
        for (let i = 0; i < inputElems.length; i++) {
            if (inputElems[i].checked) {
                let type = inputElems[i].getAttribute("grouptype");
                if (type && type === 'group') {
                    valuetrees.group.push(inputElems[i].value)
                } else {
                    valuetrees.cameras.push(inputElems[i].value)
                }
            }
        }
        if (this.props.onSubmit) {
            console.log(valuetrees);
            this.props.onSubmit(valuetrees);
        }
        this.setState({
            keyword: '',
            isSearch: false,
            showMenu: false,
            label: valuetrees.cameras.length == 0 ? "选择设备数为0" : "已选" + valuetrees.cameras.length + "个设备"
        })
    }
    OnDropList() {
        this.setState({
            showMenu: !this.state.showMenu,
        })
    }
    handleClickOutside = () => {
        this.setState({
            showMenu: false,
            keyword: '',
            isSearch: false,
        })
    }
    onCheckBoxChange(e, checked, value) {
        let aElem = getParentNodeByTag(e.target, 'a');
        function graduallyInput(aElem) {
            let nextNode = aElem.nextSibling;
            if (nextNode && nextNode.tagName.toLowerCase() == 'ul') {
                let inputElems = nextNode.querySelectorAll('input[type=checkbox]');
                for (let i = 0; i < inputElems.length; i++) {
                    inputElems[i].checked = checked;
                }
                if (nextNode) {
                    graduallyInput(nextNode);
                }
            }
        }
        graduallyInput(aElem)
    }
    onArrowChange(e) {
        let arrow = e.currentTarget.getAttribute('arrow');
        function graduallyTraverseUl(cNode) {
            let nextNode = cNode.nextSibling;
            if (nextNode && nextNode.tagName.toLowerCase() == 'ul') {
                let ulElem = nextNode;
                if (arrow == 'right') {
                    ulElem.style.display = "block"
                    cNode.setAttribute("arrow", "down");
                    if (cNode.tagName.toLowerCase() == 'a') {
                        let iElem = cNode.querySelector("i");
                        iElem && (iElem.className = "icon-arrow-down");
                    }
                } else {
                    ulElem.style.display = "none"
                    cNode.setAttribute("arrow", "right");
                    if (cNode.tagName.toLowerCase() == 'a') {
                        let iElem = cNode.querySelector("i");
                        iElem && (iElem.className = "icon-arrow-right");
                    }
                }
            }
            if (nextNode) {
                graduallyTraverseUl(nextNode);
            }
        }

        graduallyTraverseUl(e.currentTarget)

    }
    onSearchChange(e) {
        this.setState({ keyword: e.target.value });
        if (e.target.value == "") {
            this.setState({ isSearch: false });
        }
    }
    SearchTree(list, keyword) {
        let newList = [];
        list.map(item => {
            if (item.type == 'group') {
                let childrenlist = this.SearchTree(item.childrenlist, keyword)
                if (childrenlist && childrenlist.length > 0) {
                    item.childrenlist = childrenlist
                    newList.push(item)
                }

            } else {
                if (item.text.indexOf(keyword) != -1) {
                    newList.push(item)
                }
            }
        })
        return newList;
    }
    isSearched(item, list) {
        const { isSearch } = this.state;
        if (!isSearch) {
            return true;
        }
        return list.find(el => {
            if (el.value == item.value) {
                return true;
            } else {
                return this.isSearched(item, el.childrenlist)
            }
        })
    }
    HandleSearch() {
        const { keyword } = this.state;
        if (keyword == '') {
            return;
        }
        const { treeList } = this.props;
        const searchTreeList = this.SearchTree(treeList, keyword);
        this.setState({ isSearch: true, searchTreeList });
    }
    onSearchKeyUp(e) {
        if (e.which == 13) {
            this.HandleSearch();
        }
    }
    renderChildrenList(childrenlist, lv) {
        let { arrow } = this.props;
        let that = this;
        lv++;
        const { searchTreeList } = this.state;
        return childrenlist.map((item, index) => {
            let isSearched = this.isSearched(item, searchTreeList);
            let display = arrow == "right" ? "none" : "block";
            if (!isSearched) {
                display = "none"
            }
            return <ul role="menu" id="list_1" style={{ display: display }} className="menu-sub" key={index}>
                <li role="menuitem">
                    <a id={"ul_a_" + index} aria-selected="false" role="menuitemradio" className={"menu-item menu-lv" + lv} onClick={this.onArrowChange} arrow={arrow}>
                        <span>
                            {item.childrenlist.length > 0 && <i aria-live="polite" className={"icon-arrow-" + arrow}></i>}
                            <Checkbox label={item.text} value={item.value} onChange={this.onCheckBoxChange} groupType={item.type} />
                        </span>
                    </a>
                    {item.childrenlist.length > 0 && that.renderChildrenList(item.childrenlist, lv)}
                </li>
            </ul>
        })
    }
    render() {
        const that = this;
        const { treeList, arrow, hasfooter, canSearch } = this.props;
        const { searchTreeList, isSearch } = this.state;
        return (
            <div className="form-input" ref="droptree">
                <div className="tc-15-dropdown tc-15-dropdown-btn-style tc-15-menu-active">
                    <a href="javascript:void(0);" className="tc-15-dropdown-link" onClick={this.OnDropList} >
                        <em>{this.state.label}</em>
                        <i className="caret"></i>
                    </a>
                    <div className="tc-15-dropdown-menu" style={{ display: !this.state.showMenu ? "none" : "block" }}>

                        {
                            canSearch &&
                            <div className="search-box search-box-simple m">
                                <div className="search-input-wrap">
                                    <input placeholder="搜索设备名" className="tc-15-input-text search-input" style={{ paddingLeft: "10px" }} onChange={this.onSearchChange} onKeyUp={this.onSearchKeyUp} />
                                </div>
                                <input type="button" value="搜索" className="search-btn" onClick={this.HandleSearch} />
                            </div>
                        }
                        <div className="mod-team-list">
                            <dl id="aside_nav" aria-orientation="vertical" role="menu" aria-activedescendant="list_a_2" className=" ">
                                {treeList.map((item, index) => {
                                    let isSearched = this.isSearched(item, searchTreeList)
                                    return <dd role="menuitem" aria-haspopup="false" aria-expanded="true" key={index} style={{ display: isSearched ? "block" : "none" }}>
                                        <a aria-selected="false" role="menuitemradio" id="list_a_0" aria-controls="list_0" className="menu-item menu-lv2" onClick={this.onArrowChange} arrow={arrow}>
                                            <span>
                                                {item.childrenlist.length > 0 && <i aria-live="polite" className={"icon-arrow-" + arrow}></i>}
                                                <Checkbox label={item.text} value={item.value} onChange={this.onCheckBoxChange} groupType={item.type} />
                                            </span>
                                        </a>
                                        {item.childrenlist.length > 0 && that.renderChildrenList(item.childrenlist, 2)}
                                    </dd>
                                })}
                            </dl>
                        </div>

                        <div className="btn-group" style={{ display: (!hasfooter || treeList.length <= 0 || (isSearch && searchTreeList.length <= 0)) ? "none" : "block" }}>
                            <button className="tc-15-btn m" onClick={this.onSubmit}>确定</button><button className="tc-15-btn weak m" onClick={() => {
                                this.setState({
                                    showMenu: false,
                                    keyword: '',
                                    isSearch: false
                                })
                            }}>取消</button>
                        </div>
                        {treeList.length <= 0 &&
                            <div style={{ padding: "10px" }}>
                                暂无设备，请前往设备管理添加设备
                        </div>}
                        {
                            isSearch && searchTreeList.length <= 0 &&
                            <div style={{ padding: "10px" }} className="text-weak">
                                无对应结果
                        </div>
                        }
                    </div>
                </div>
            </div>
        )
    }
}
export default enhanceWithClickOutside(DropDownTree)