import React from 'react'
import { connect } from 'react-redux'




//权限结构块
class AccountAuthorityList extends React.Component {
  renderScope(scope, index) {
    const { scopesList } = this.props
    let info = scopesList.find(el => {
      return el.scope == scope.trim()
    })
    if (info) {
      return <span className="text-overflow" key={index}>{info.description}</span>
    }
  }
  render() {
    const { scopes } = this.props
    if (scopes == null) {
      return null
    }
    let scopesList = scopes.split(',')
    return (
      <div><p className="text-label">
        {
          scopesList.map((el, index) => {
            return this.renderScope(el, index)
          })
        }
      </p></div>
    );
  };
}

export default connect(function (state) { return state })(AccountAuthorityList)
